h1{color:blue}

*{margin:0;padding:0}body{background:#a4d9f9;font-family:'Josefin Sans', sans-serif}.wrapper{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.container{width:280px;height:350px;background:#fff;position:relative;border-radius:10px;box-shadow:0 2px 10px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12)}.container .profile-img{width:100px;height:100px;border-radius:50%;position:absolute;top:-65px;left:85px;border:5px solid #a4d9f9}.content{padding:65px 20px 20px}.content .sub-content{text-align:center}.content .sub-content h1{text-transform:uppercase;font-size:24px;color:#1da1f2}.content .sub-content span{font-size:12px;color:#494949}.content .sub-content p{margin-top:15px;font-weight:600}.content .sub-content span.location{display:block;margin:5px 0 20px}.content .sub-content span.location .fa{font-size:18px;margin-right:5px}.content .sub-content a{color:#1da1f2}.data{width:100%;overflow:hidden;margin:30px 0 20px}.btn{clear:both;text-align:center;width:100%;height:35px;background:#1da1f2;color:#fff;text-transform:uppercase;line-height:38px;font-weight:600;border-radius:10px;text-shadow:0 0 10px rgba(0,0,0,0.5)}

header{background-color:aliceblue;text-align:center;padding:4rem}header h1{margin-bottom:1rem}

